<template>
<div>
    <div class="panel panel-info">
        <div class="panel-body">
            <div>设备部署节点管理</div>
            <div class="buttonbox" v-show="!inforadmin">
                <button type="button" class="btn btn-info" @click="inforAddClick(true)" v-if="$api.opreate.hasLoginComponents(this,'sys:page:add')">新增</button>
                <button type="button" class="btn btn-warning" @click="updateData" v-if="$api.opreate.hasLoginComponents(this,'sys:page:modify')">修改</button>
                <button type="button" class="btn btn-danger" @click="deleteData" v-if="$api.opreate.hasLoginComponents(this,'sys:page:delete')">删除</button>
                <div class="pull-right col-sm-3 search-input" v-if="$api.opreate.hasLoginComponents(this,'sys:page:search')">
                    <div class="col-sm-12">
                        <div class="col-sm-8 search-input ">
                            <input type="text" class="form-control" placeholder="搜索用户">
                        </div>
                            <input type="button" value="搜索" class="btn btn-info col-sm-3" @click="searchBtnClick">
                    </div>
                        </div>
                    </div>
                    <tables v-show="!inforadmin" :tableSelectData="tableSelectData"></tables>
                    <div v-show="inforadmin">
                        <form class="form form-horizontal col-sm-8" ref="refForm">
                            <div class="form-group">
                                <label for="" class="col-sm-2 control-label">节点类别</label>
                                <div>
                                    <div class="col-sm-2">
                                        <input type="radio" name="type" id="type1" value="0" v-model="deployNodeType" :disabled="opreate=='update'"><label for="type1" class="control-label">场景节点</label>
                                    </div>
                                    <div class="col-sm-2">
                                        <input type="radio" name="type" id="type2" value="1" :checked="true" v-model="deployNodeType" :disabled="opreate=='update'"><label for="type2"  class="control-label">设备节点</label>
                                    </div>
                                </div>
                            </div>
                            <div class="form-group">
                                <label class="control-label col-sm-2">节点名称</label>
                                <div class="col-sm-6" :class="{'has-success':!errors.has('name')&&addData.node_name!='','has-error':errors.has('name')}">
                                    <input type="text" class="form-control" name="name" :placeholder="'请输入节点名称,最多为30位'" v-model="addData.node_name" :data-vv-as="'节点名称'" v-validate="$api.opreate.validateAdd('name',30,stateid,opreate,'equipment')">
                                    </div>
                                    <div class="text-danger">
                                        <span v-show="errors.has('name')">{{errors.first("name")}}</span>
                                    </div>
                                </div>
                                <div class="form-group" v-if="deployNodeType=='0'">
                                    <label class="control-label col-sm-2">部署地址</label>
                                    <div class="col-sm-6">
                                        <select type="text" name="url" class="form-control"  v-model="addData.address_id"><option :value="item.addressId" v-for="(item,index) of sceneAddress" :key="index" v-text="item.address"></option> </select></div>
                                </div>
                                <div class="form-group" v-else-if="deployNodeType=='1'">
                                    <label class="control-label col-sm-2">父级节点</label>
                                    <div class="col-sm-6">
                                        <select type="text" name="url" class="form-control"  v-model="addData.parent_id"><option :value="item.deployNodeId" v-for="(item,index) of deployNode" :key="index" v-text="item.nodeName"></option> </select></div>
                                </div>
                                <div class="form-group">
                                    <label class="control-label col-sm-2">用途说明</label>
                                    <div class="col-sm-6" :class="{'has-success':!errors.has('explain')&&addData.purpose_explain!='','has-error':errors.has('explain')}">
                                        <textarea rows="6" class="form-control" name="explain" :placeholder="'请输入用途说明信息'" v-model="addData.purpose_explain" :data-vv-as="'用途说明信息'" v-validate="'required'"></textarea>
                                    </div>
                                    <div class="text-danger">
                                        <span v-show="errors.has('explain')">{{errors.first("explain")}}</span>
                                    </div>
                                </div>
                                <div class="form-group">
                                    <label class="control-label col-sm-2">内部位置</label>
                                    <div class="col-sm-6" :class="{'has-success':!errors.has('location')&&addData.inside_location!='','has-error':errors.has('location')}">
                                        <textarea rows="6" class="form-control" name="location" :placeholder="'请输入内部位置信息'" v-model="addData.inside_location" :data-vv-as="'内部位置信息'" v-validate="'required'"></textarea>
                                    </div>
                                    <div class="text-danger">
                                        <span v-show="errors.has('location')">{{errors.first("location")}}</span>
                                    </div>
                                </div>
                                <div class="form-group">
                                    <label class="control-label col-sm-2">描述</label>
                                    <div class="col-sm-6" :class="{'has-success':!errors.has('remarks')&&addData.remarks!='','has-error':errors.has('remarks')}">
                                        <textarea rows="6" class="form-control" name="remarks" :placeholder="'请输入描述信息'" v-model="addData.remarks" :data-vv-as="'描述信息'" v-validate="'required'"></textarea>
                                    </div>
                                    <div class="text-danger">
                                        <span v-show="errors.has('remarks')">{{errors.first("remarks")}}</span>
                                    </div>
                                </div>
                                <div class="form-group col-sm-12">
                                    <button type="button" class=" col-sm-2 col-sm-offset-2 btn btn-info" @click="addDatas">提交</button>
                                    <button type="button" class=" col-sm-2 col-sm-offset-2  btn btn-warning" @click="inforAddClick(false)">返回</button>
                                </div>
                        </form>
                    </div>
                </div>
            </div>
        </div>
</template>

部署节点

<script>
import tables from "@/components/table.vue";
export default {
    data: function () {
        return {
            inforadmin: false,
            opreate: "add",
            tableSelectData: {
                selectTableurl: "equipment/selectDeployNode.action",
                pageloginData: {
                    id: this.stateid
                },
                filterName: []
            },
            addData: {
                address_id: "", //地址id
                node_name: "", //节点名称
                purpose_explain: "", //用途说明
                inside_location: "", //内部位置
                remarks: "", //描述
                parent_id: "" //父级节点
            },
            deployNodeType: "0",
            sceneAddress: [], //部署地址数组
            deployNode: [{
                deployNodeId: "1",
                nodeName: "123"
            }], //父级节点
        }
    },
    props: {
        stateid: String,
        statetitle: String
    },
    computed: {
        checkData() {
            return this.$store.getters.GETUSERTABLEPAGECHECKDATA;
        },
    },
    components: {
        tables,
    },
    mounted() {
        this.loginSceneAddress();
    },
    watch: {
        deployNodeType(value) {
            if (value == '1') {
                this.loginDeployNode();
            }
        }
    },
    methods: {
        //添加按钮按下
        inforAddClick(bool) {
            var vm = this;
            var opreate = this.opreate;
            if (opreate == 'update') {
                this.addData = this.$api.opreate.ObjectCopy(this.checkData[0]);
            }
            async function Empty() {
                for (var key in vm.addData) {
                    vm.addData[key] = "";
                }
                return true;
            }
            Empty().then(() => {
                vm.opreate = "add";
                vm.inforadmin = bool;
                vm.$validator.errors.items=[]; //将表单错误数组重置为0
            })
        },
        //获取场景地址
        loginSceneAddress() {
            var vm = this;
            vm.$axios.post(vm.HOST + "equipment/selectAllAddress.action").then(function (data) {
                if (data.bool) {
                    vm.sceneAddress = data.data.list;
                }
            }).catch(function (error) {
                console.log(error)
            })
        },
        loginDeployNode() {
            var vm = this;
            vm.$axios.post(vm.HOST + "equipment/selectSceneNode.action").then(function (data) {
                if (data.bool) {
                    vm.deployNode = data.data.list;
                }
            }).catch(function (error) {
                console.log(error)
            })
        },
        // 修改按钮按下
        updateData() {
            var vm = this;
            if (vm.$api.opreate.TESTcheck(vm, "修改")) {
                this.inforadmin = true;
                this.opreate = "update";
                this.addData = this.$api.opreate.ObjectCopy(this.checkData[0]);
                console.log(this.addData.parent_id!="null"&&this.addData.parent_id!="")
                if(this.addData.parent_id!="null"&&this.addData.parent_id!=""){
                    this.deployNodeType=1;
                }
            }
        },
        //添加函数
        addDatas() {
            var vm = this;
            var stateId = vm.stateid;
            var data = vm.addData;
            var datas = {
                data: data,
                states: {
                    id: stateId
                }
            };
            // 修改操作
            async function addDataFun() {
                var bool;
                if (vm.opreate == "update") {
                    bool = vm.$api.http.opreateHttp(vm, "equipment/modifyDeployNode.action", datas, "修改", "");
                } else if (vm.opreate == "add") {
                    //添加操作
                    bool = vm.$api.http.opreateHttp(vm, vm.deployNodeType == '0' ? "equipment/addDeployNode.action" : "equipment/addEquipmentNode.action", datas, "添加", "");
                }
                return bool;
            }
            addDataFun().then(function (success) {
               
                        vm.inforAddClick(false);
            
            })
        },
        // 删除按钮按下
        deleteData() {
            var vm = this;
            var checkData = vm.$store.getters.GETUSERTABLEPAGECHECKDATA;
            if (vm.$api.opreate.TESTcheck(vm, "删除")) {
                //询问框
                layer.confirm(
                    "是否删除", {
                        btn: ["确认", "取消"] //按钮
                    },
                    function (index, layero) {
                        var datas = {
                            id: checkData[0].deploy_node_id,
                            states: {
                                id: vm.stateid
                            }
                        };
                        vm.$api.http.opreateHttp(
                            vm,
                            "equipment/deleteDeployNode.action",
                            datas,
                            "删除"
                        );
                    }
                );
            }
        },
        //搜索按钮按下
        searchBtnClick() {
            var vm = this;
            if (vm.searchData.data != "") {} else {
                vm.$layer.msg("请输入场景地址名后搜索", {
                    icon: 0
                });
            }
        }
    }
};
</script>

<style>

</style>



